.pfeil {
  
    height: 60vmin;
  }
  
  
  
  .Logo {
    -webkit-animation: windrose-spin infinite 20s linear;
            animation: windrose-spin infinite 20s linear;
    height: 25vmin;
  }
  
  .Header {
    margin: auto auto 0 auto;

    /* height: 10vh; */
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    /* font-size: calc(10px + 2vmin); */
    color: white;
    width: 100vw;
  }
  
  
  @-webkit-keyframes windrose-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  
  @keyframes windrose-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  
  @-webkit-keyframes pfeil-rechts {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  
  @keyframes pfeil-rechts {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
 
  
